
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  font-family: 'Hiragino Kaku Gothic ProN', 'ヒラギノ角ゴ ProN W3', 'Noto Sans CJK JP', 游ゴシック体, 'Yu Gothic', YuGothic, Meiryo, 'メイリオ';
  color: #424141;
  margin: 0;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
p {
  margin: 0;
}
/*float*/
.fright {
  float: right;
}
.fleft {
  float: left;
}
/*clearfix*/
.cf:before,
.cf:after {
  content: " ";
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  *zoom: 1;
}
@-webkit-keyframes hide {
0% { opacity: 1;
}
100% { opacity: 0; display: none;
}
}
@keyframes hide {
0% { opacity: 1;
}
100% { opacity: 0; display: none;
}
}
@-webkit-keyframes show {
100% { opacity: 1;
}
0% { opacity: 0;
}
}
@keyframes show {
100% { opacity: 1;
}
0% { opacity: 0;
}
}
a:-webkit-any-link {
  text-decoration: none;
}
.container {
  width: 1000px;
  margin: 0 auto;
}
/*for 画面遷移*/
.fade-before-leave {
  opacity: 0;
}
/*.fade-enter, .fade-leave {
  opacity: 0;
}*/
.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter {
  opacity: 0;
}

/*for SP*/
@media (max-width: 768px) {
#app {
    margin-bottom: 44px;
}
.container {
    width: 100%;
    padding: 15px;
}
}



