/*animate*/
@-webkit-keyframes flipInX {
from {
    -webkit-transform: perspective(300px) rotate3d(1, 0, 0, 70deg);
            transform: perspective(300px) rotate3d(1, 0, 0, 70deg);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    opacity: 0;
}
40% {
    -webkit-transform: perspective(300px) rotate3d(1, 0, 0, -4deg);
            transform: perspective(300px) rotate3d(1, 0, 0, -4deg);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
}
60% {
    -webkit-transform: perspective(300px) rotate3d(1, 0, 0, 2deg);
            transform: perspective(300px) rotate3d(1, 0, 0, 2deg);
    opacity: 1;
}
80% {
    -webkit-transform: perspective(300px) rotate3d(1, 0, 0, -1deg);
            transform: perspective(300px) rotate3d(1, 0, 0, -1deg);
}
to {
    -webkit-transform: perspective(300px);
            transform: perspective(300px);
    opacity: 1;
}
}
@keyframes flipInX-data-v-7ee97150 {
from {
    -webkit-transform: perspective(300px) rotate3d(1, 0, 0, 70deg);
            transform: perspective(300px) rotate3d(1, 0, 0, 70deg);
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
